/*
$primary: #B5D43B;
$secondary: #434738; 
$black: #000;
$white: #FFFFFF;
$thirdOption: #F7F7F7;
$greenLight: #D9EF82;
$background: #252525;
*/
.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #1FBABF;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #252525;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0;
}
.header__heading {
  text-align: center;
  color: #FFFFFF;
  margin: 0 0 2rem 0;
  font-size: 3.2rem;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation__link {
  margin-bottom: 2rem;
  color: #FFFFFF;
  padding: 1rem;
  transition: background-color 300ms;
}
.navigation__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navigation__link {
    margin-bottom: 0;
  }
}
.navigation__link:hover {
  background-color: #1FBABF;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
    align-items: center;
    gap: 2rem;
  }
}

.about {
  padding: 2rem;
  color: #0B759D;
  background-color: #1FBABF;
}
.about__heading {
  text-align: center;
  margin: 0;
  font-weight: 700;
}
@media (min-width: 768px) {
  .about__heading {
    text-align: start;
  }
}
.about__text {
  color: #FFFFFF;
  text-align: justify;
  margin-bottom: 3rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .about__text {
    text-align: justify;
  }
}
@media (min-width: 768px) {
  .about__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .about__image {
    width: 50%;
  }
}

.services {
  padding: 2rem;
}
.services__heading {
  margin: 3rem 0 0 0;
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .services__heading {
    margin: 3rem 0 4rem 0;
  }
}
.services__image {
  width: 30rem;
  margin-bottom: 3rem;
}
.services__content {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .services__content {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    align-items: center;
  }
}
.services__card {
  padding: 2rem;
  background-color: #60D3AA;
  border-radius: 2rem;
  margin-bottom: 3rem;
  opacity: 1;
}
@media (min-width: 768px) {
  .services__card {
    grid-column: 2/3;
    grid-row: 1/2;
    margin-bottom: 0;
    opacity: 0;
    transition: opacity 1.5s;
  }
}
@media (min-width: 768px) {
  .services__card--show {
    opacity: 1;
  }
}
.services__title {
  margin-bottom: 2rem;
  text-align: center;
  text-transform: uppercase;
}
.services__text {
  margin: 0;
  text-align: justify;
}
.services__symbol {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  transition: background-color 300ms;
}
.services__symbol:hover {
  background-color: #1FBABF;
}
@media (min-width: 768px) {
  .services__symbol {
    font-size: 4rem;
    color: #000;
    margin: 0;
    padding: 0;
    visibility: visible;
  }
  .services__symbol--left {
    justify-self: center;
  }
  .services__symbol--right {
    justify-self: center;
  }
}

.projects {
  margin-top: 3rem;
}
.projects__container {
  background-color: #1FBABF;
  margin-top: -0.1rem;
}
.projects__content {
  padding: 3rem;
}
@media (min-width: 1024px) {
  .projects__container {
    margin-top: -4rem;
  }
}
@media (min-width: 1024px) {
  .projects__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.projects__wave {
  margin-top: 10rem;
  width: 300rem;
}
.projects__heading {
  color: #FFFFFF;
  text-align: center;
}
.projects__card {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  gap: 2rem;
  background-color: #FFFFFF;
  border-radius: 2rem;
  margin-bottom: 3rem;
  transition: transform 300ms;
}
.projects__card:last-of-type {
  margin-bottom: 0;
}
.projects__card:hover {
  transform: scale(1.1);
}
@media (min-width: 1024px) {
  .projects__card {
    margin-bottom: 0;
  }
}
.projects__image {
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  object-fit: cover;
  padding: 0.5rem;
  margin: 0 auto;
  background-color: #1FBABF;
  border: 1rem solid #252525;
}
@media (min-width: 768px) {
  .projects__image {
    width: 30rem;
    height: 30rem;
  }
}
.projects__title {
  text-align: center;
  color: #0B759D;
  margin: 0;
}
.projects__links {
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.projects__link {
  padding: 1rem;
  background-color: #60D3AA;
  color: #FFFFFF;
  border-radius: 1rem;
}
.projects__link:hover {
  background-color: #2ca177;
}

.tech__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 0;
  align-items: center;
  justify-content: center;
}
.tech__link {
  font-size: 1.3rem;
  border-radius: 1rem;
  padding: 1rem;
  background-color: #252525;
  color: #FFFFFF;
  list-style: none;
  font-weight: 700;
  /*
  &--html{
      color: coral;
  }

  &--scss{
      color: pink
  }

  &--css{
      color: aquamarine;
  }

  &--git{
      color: orange;
  }

  &--gulp{
      color: cornflowerblue;
  }

  &--responsive{
      color:darkseagreen;
  }

  &--bem{
      color:yellowgreen;
  }
  */
}

.contact {
  padding: 2rem;
}
.contact__heading {
  text-align: center;
  margin: 2rem 0 2rem 0;
  color: #FFFFFF;
}
.contact__form {
  font-size: 2rem;
  display: flex;
  flex-direction: column;
}
.contact__input {
  display: flex;
  margin-bottom: 2rem;
  flex-direction: column;
}
.contact__input input:not([type=submit]),
.contact__input textarea {
  border: 0.1rem solid #e1e1e1;
  padding: 1rem;
}
.contact__input textarea {
  resize: none;
  font-size: 1.5rem;
}
.contact__label {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .contact__labeL {
    flex-basis: 10rem;
  }
}
.contact__button {
  padding: 1rem;
  font-weight: 700;
  max-width: 40rem;
  align-self: center;
  width: 100%;
  border-radius: 1rem;
  transition: background-color 300ms;
}
.contact__button:hover {
  background-color: #1FBABF;
}